import { GoogleSpreadsheet } from 'google-spreadsheet';
import { Section } from '@/types';

const doc = new GoogleSpreadsheet(process.env.GOOGLE_SHEETS_SPREADSHEET_ID!);

// Inicializar la conexi√≥n
async function initializeSheet() {
  await doc.useServiceAccountAuth({
    client_email: process.env.GOOGLE_SHEETS_CLIENT_EMAIL!,
    private_key: process.env.GOOGLE_SHEETS_PRIVATE_KEY!.replace(/\\n/g, '\n'),
  });
  await doc.loadInfo();
}

// Actualizar un campo individual
export async function updateField(date: string, sectionId: string, fieldId: string, value: any) {
  await initializeSheet();
  const sheet = doc.sheetsByIndex[0];
  
  // Buscar la fila correspondiente a la fecha
  const rows = await sheet.getRows();
  const rowIndex = rows.findIndex(row => row.date === date);
  
  if (rowIndex === -1) {
    // Si no existe la fila, la creamos
    await sheet.addRow({
      date,
      [`${sectionId}_${fieldId}`]: value
    });
  } else {
    // Si existe, actualizamos el campo espec√≠fico
    rows[rowIndex][`${sectionId}_${fieldId}`] = value;
    await rows[rowIndex].save();
  }
}

// Actualizar las notas diarias
export async function updateNotes(date: string, notes: string) {
  await initializeSheet();
  const sheet = doc.sheetsByIndex[0];
  
  const rows = await sheet.getRows();
  const rowIndex = rows.findIndex(row => row.date === date);
  
  if (rowIndex === -1) {
    await sheet.addRow({
      date,
      notes
    });
  } else {
    rows[rowIndex].notes = notes;
    await rows[rowIndex].save();
  }
}

// Obtener datos de un d√≠a espec√≠fico
export async function getDayData(date: string) {
  await initializeSheet();
  const sheet = doc.sheetsByIndex[0];
  
  const rows = await sheet.getRows();
  return rows.find(row => row.date === date);
} 