'use client';

import { useState } from 'react';
import { updateNotes } from '@/utils/spreadsheet';

interface UpdateButtonProps {
  onUpdate: () => Promise<void>;
  isYesterday?: boolean;
  sections: any[];
  date: string;
}

export const UpdateButton: React.FC<UpdateButtonProps> = ({ onUpdate, isYesterday, sections, date }) => {
  const [isUpdating, setIsUpdating] = useState(false);

  const handleUpdate = async () => {
    setIsUpdating(true);
    try {
      // Obtener las notas de la secci√≥n correspondiente
      const notesSection = sections.find(s => s.id === 'notes');
      const notes = notesSection?.fields[0].value as string;

      // Actualizar solo las notas
      await updateNotes(date, notes);
      
      alert('Notes updated successfully!');
    } catch (error) {
      console.error('Update failed:', error);
      alert('Failed to update notes. Please try again.');
    } finally {
      setIsUpdating(false);
    }
  };

  return (
    <button
      onClick={handleUpdate}
      disabled={isUpdating}
      className={`
        w-full
        px-6 py-2 
        rounded-full 
        bg-[var(--button-success)] 
        text-white 
        font-medium 
        shadow-lg 
        hover:opacity-90 
        transition-all
        disabled:opacity-50 
        disabled:cursor-not-allowed
        flex items-center justify-center gap-2
        mt-8
      `}
    >
      {isUpdating ? (
        <>
          <span className="animate-spin">‚ü≥</span>
          <span>Updating...</span>
        </>
      ) : (
        <>
          <span>‚Üë</span>
          <span>Update {isYesterday ? 'Yesterday' : 'Today'}</span>
        </>
      )}
    </button>
  );
}; 